@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: local('Raleway Thin'), local('Raleway-Thin'), url(https://fonts.gstatic.com/s/raleway/v14/1Ptsg8zYS_SKggPNwE44TYFq.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 200;
  font-display: swap;
  src: local('Raleway ExtraLight'), local('Raleway-ExtraLight'), url(https://fonts.gstatic.com/s/raleway/v14/1Ptrg8zYS_SKggPNwOIpWqZPAA.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Raleway'), local('Raleway-Regular'), url(https://fonts.gstatic.com/s/raleway/v14/1Ptug8zYS_SKggPNyC0ITw.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}



* {
  margin: 0;
  padding: 0;
  overflow: hidden;
  color: white;
}

.App {
  background-color: rgb(30, 30, 30);
}

/* Mobile Styling */

.nav {
  position: absolute;
  padding: 2%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-items: center;
  background-color: rgb(40, 40, 40);
  z-index: 69;
  width: 100%;
  height: 10%;
}
.nav-img {
  max-height: 100%;
  max-width: 100%;
  z-index: 420;
}

.center {
  position: absolute;
  top: 58%;
  left: 50%;
  height: 100%;
  margin-top: -100%;
  margin-left: -75%;
}

.selected {
  background-color: rgb(48, 48, 255);
}

footer {
  position: absolute;
  background-color: rgb(40, 40, 40);
  /* border: 2px solid white; */
  border-radius: 30px;
  height: 12%;
  width: 97%;
  bottom: -6%;
  display: inline-block;
  margin: 0 1%;
}

.bar {
  position: absolute;
  background-color: white;
  width: 30%;
  height: 5%;
  right: 35%;
  border-radius: 20%;
  top: 13%;
  z-index: 50;
}

/* Desktop Stying */

/* .deskApp {
  margin: 20px;
} */

.deskHead {
  box-sizing: border-box;
  padding: 5%;
  padding-top: 2%;
  font-family: 'Raleway', sans-serif;
  font-weight: 100;
  width: 100%;
  background-color: #282828;
  text-align: center;
  color: white;
  height: 100px;
}

.body {
  margin: 20px;
  font-family: 'Raleway', sans-serif;
}

.logos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "firefox chrome safari";
  justify-items: center;
  align-items: center;
  height: 70px;
}

h3 {
  margin: 5px;
}

.img {
  max-width: 100%;
  height: 50px;
  margin: auto;
}

.firefox {
  grid-area: firefox;
  object-fit: contain;
  max-height: 50px;
}

.chrome {
  grid-area: chrome;
  object-fit: contain;
  max-width: 100%;
  height: 100%;
}

.safari {
  grid-area: safari;
  object-fit: contain;
  max-width: 100%;
  height: 100%;
}

button {
  border: none;
  background-color: rgb(30, 30, 30);
}